import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

@Component({
  tag: 'bh-page-navigation-child',
  styleUrl: 'bh-page-navigation-child.css',
  shadow: true,
})
export class BhPageNavigationChild {
  /**
   * Label text for the navigation item
   */
  @Prop() label!: string;

  /**
   * Optional badge text/number to display
   */
  @Prop() badge?: string | number;

  /**
   * Whether to show the badge (when badge prop is provided)
   */
  @Prop() showBadge: boolean = true;

  /**
   * Whether the item is currently active/selected
   */
  @Prop() isActive: boolean = false;

  /**
   * Event emitted when the navigation item is clicked
   */
  @Event() bhItemClick!: EventEmitter<void>;

  private handleClick = () => {
    this.bhItemClick.emit();
  };

  private getItemClasses() {
    return {
      'page-navigation-child': true,
      'active': this.isActive,
    };
  }

  render() {
    return (
      <Host>
        <div class={this.getItemClasses()} onClick={this.handleClick}>
          <div class="page-navigation-child-content">
            <span class="page-navigation-child-label">{this.label}</span>
            {this.badge !== undefined && this.showBadge && (
              <span class="page-navigation-child-badge">{this.badge}</span>
            )}
          </div>
        </div>
      </Host>
    );
  }
}
